@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
/*base styles*/
*{
  margin:0px;
  font-family: "Quicksand";
  color:#333;
}

.navbar{
  padding: 20px;
  display:flex;
  align-items:center;
  max-width:600px;
  margin:0 auto;
  border-bottom: 3px solid #f2f2f2;   
} 
.navbar h1{
  color: #f1356d
}
.navbar .links{
  margin-left: auto;
}

.navbar a{
  margin-left: 16px;
  text-decoration: none;
  padding:6px;
}

.navbar a:hover{
  color: #f1356d
}
.content{
  max-width:600px;
  margin: 48px auto;
  padding: 20px;
}
.blog-preview{
  padding: 10px,16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
} 
.blog-preview :hover{
  box-shadow:  1px 3px 5px rgb(0,0,0);
}

.blog-preview h1{
  font-size: 40px;
  color: chartreuse;
  margin-bottom: 8px;
}

.blog-preview a{
  text-decoration: none;
} 

/* blog details page */

.blog-details h2{
  font-size:20px;
  color:#f1357d;
  margin-bottom:10px;
}

.blog-details div{
  margin:20px 0;
}

.blog-details button{
  background: #f1356d;
  color: #fff;
  border: 0;
  border-radius: 4px;
  cursor:pointers
}

/* create new blog form */
.create{
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
}

.create label{
  text-align:left;
  display: block;
}
.create h2{
  font-size: 40px;
  margin-bottom: 30px;
  color: #f1356d;
}

.create input, .create textarea, .create select{
  width:100% ;
  margin: 10px 0;
  padding: 6px 10px;
  border: 1px solid rgb(219, 215, 215);
  box-sizing: border-box;
  display: block;
}

.create button{
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 6px;
  cursor: pointer;
}

/* 404 page */

.not-found{
  padding: 30px;
  text-align:center;
}

.not-found a {
  text-decoration: none;
  display: flex;
  
  margin-top: 150px;
}

.not-found a:hover{

  color: #f1356d;
}